
.player {
  padding-bottom: 20px;
  padding-top: 20px;
}
.player .info {
  text-align: left;
}
.player .score {
  color: #d0c1c1
}
.player .lastScore {
  opacity: 0;
  position: absolute;
  margin-left: -20px;
}
.progress-bar, .camel {
  height: 50px;
}
.camel {
  background: url('/static/camel.png') left center no-repeat;
  -webkit-animation: play .8s steps(12) infinite;
          animation: play .8s steps(12) infinite;
  width: 75px;
}
@-webkit-keyframes play {
100% { background-position: -900px;
}
}
@keyframes play {
100% { background-position: -900px;
}
}
.fade-enter {
  opacity: 1 !important;
}
.fade-enter-to {
  -webkit-transform: scale(10, 10);
          transform: scale(10, 10);
}
.fade-enter-active {
  transition: all 1.5s !important;
}



